@import '../../base';
.footer{
  padding-top: 120px;
  
  @media screen and (max-width: 1000px) {
    width: 100%;
    .margin-250{
      margin-top:0px !important;
    }
    .col-12{
      padding: 0 !important;
    }
    .sub-footer{
      padding-left: 10px !important;
      padding-right: 10px !important;
    }
  }
  .center-align{
    text-align: center;
  }
  .allow-float-left{
    float:left;
    @media screen and (max-width: 1280px){
      &:nth-last-child(1){
        float:none;
        padding: 0;
      }
    }
    @media screen and (max-width: 980px){
      &:nth-last-child(2){
        float:none;
        padding: 0;
      }
    }
  }
  .margin-250{
    margin-top: -130px
  }
  .sub-footer{
    margin: auto;
    padding-top: 0px;
    padding-bottom: 60px;
    z-index:10;
    margin-bottom: 0;
  }
  .padding-top-60{
    padding-top:60px;
    margin: 0px !important;
    padding-bottom: 10px;
    @media screen and (min-width: 1000px) {
      padding-bottom: 40px;
    }
      .row{
        // margin: 0;
        @media screen and (max-width:1000px) {
          margin-right: 0px !important;
          margin-left: 0px !important;
      }
    }
  }
  .margin-left-10{
    margin-left: 10px;
  }
  .cta{
    .footer-fancy-font{
      font-family: $font-after-night;
      font-size: 43px;
      color: $color-body;
    }
    p{
      font-family: $font-open-sans;
      color:$color-body;
      font-size: 27px;
    }
  }
  .cta-button {
    margin: auto;
  }
  .footer-bg{
    z-index: -6;
    width:100vw;
    position: absolute;
    transform: translateY(-100%);
  }

  .button-medium-flat{
    margin-top:1.75em;
    margin-bottom: 5%;
    margin-right:40px;
    font-size: $font-size-smallest;
    font-weight: 600;
    width: 200px;
    height: 72px;
    border-radius: 36px;
    border: 0 !important;
    margin: 10px;

    @media screen and(max-width: 1000px){
      margin: 10px auto;
    }
  }
  .green-btn {
    background-image: linear-gradient(106deg, #00d7af, #46737a);
    box-shadow: 0 20px 25px 0 rgba(70, 115, 122, 0.15);
    p{
      color:#ffffff !important;
    }
  }
  .white-btn{
    background-color: #ffffff;
    box-shadow: 0 20px 25px 0 rgba(70, 115, 122, 0.1);
  } 
  .footer-bg-color {
    background: #f2faf9;
    height: 100%;

    a {
      &:active {
        border: none;
      }
      &:hover {
        text-decoration: none;
        color: #00d7af;
      }
    }
  }
  /*Footer Links styling*/
  .nav-item{
    list-style-type: none;
    .nav-link{
      text-align: center;
    }
  }
  .footer-links{
    color: #99bbc1;
    padding-top:10px;
    padding-bottom: 40px;
    ul{
      list-style-type: none;
      padding:0px;
      @media screen and (max-width: 1000px){
        width: 80% !important;
        text-align: left !important;
        margin-left: 20% !important; 
      }
      li{
        padding-top:15px;
      }
      button, a.link{
        color:$color-caption;
        font-size: $font-size-smallest;
        font-weight: $font-weight-semibold;
      }
    }
    .col-md-6{
      padding:0px;
    }
  }
  .icon{
    vertical-align: middle;
    margin-top: 4px;
    width: 20px;
    height: 20px;
    margin-left: 0px;

    @media screen and(max-width: 1000px){
      margin-left: 11px;
    }
  }
  .social-media-icons{
    .col-md-3{
      padding:0px;
    }
    @media screen and(max-width: 1000px){
      width: 40px;
      height: 40px;
      .col-md-3{
        float: right !important;
        padding:0 0 0 10px !important;
      }
    }
  }
  .dropdown {
    @media screen and(max-width: 1000px){
      font-size:$font-size-small;
      color: #99bbc1;
      font-weight: $font-weight-bold;
    }
    .mobile-footer-header{
      padding-top: 8px !important;
    }
    .nav-link {
      @media screen and(max-width: 1000px){
        .center-align {
          color:$color-caption !important;
          margin: auto;
          width: 130px;
          height: 20px;

          span {
            float: left;
          }
        }
        button{
          &:active, :focus{
            //background-color: $color-form;
            background-image:
            radial-gradient(
              circle at center,
              rgba(242, 250, 249, 1),
              rgba(242, 250, 250, 1)
            );
          }
        }
        .arrow{
          color:#354f53;
          transition: 0.2s ease-in-out;
          float: right;
          padding: 5px;
        }
        .turn{
          color:#354f53;
          transform: rotate(180deg) !important;
          transition: 0.2s ease-in;
          float: right;
          padding: 5px;
        }

        span{
          img{
            margin-left: 5%;
          }
        }
      }
    }
    .sub-menu{
      @media screen and(max-width: 1000px){
        padding-left: -10px;
        padding-right: -10px;
        background-color:#ffffff;
        a{
          text-align: left !important;
          button{
            color: #99bbc1;

            p {
              width: 130px;
              margin: 0 auto;
              font-size: $font-size-smallest;
            }
          }
          .dropdown-item {
            &:hover {
              background-color: rgba(0,0,0,0);
            }
          }
        }
      }
    }
  }
  .footer-link-header{
    padding: 0;
    font-size: $font-size-small;
    font-weight: $font-weight-bold;
    @media screen and(max-width: 1000px){
      font-size: $font-size-smallest;
      font-weight: $font-weight-bold;
      color: #99bbc1;
      padding:0px;
    }
  }
  .footer-contact{
    margin: 0 auto !important;

    .location-row {
      margin: 0 auto !important;

      @media screen and (min-width: 1000px){
        width: 400px;
      }

      div {
        max-width: 240px;
        margin: auto;
        display: inline;

        img {
          float: left;
        }
      }
    }

    div{
      padding-top:2px;
      padding-bottom:10px;
    }
    p{
      font-size: $font-size-small;
      font-weight: bold;
      color:#00d7af;
      margin-left: 40px;
      @media screen and(min-width:1000px) {
        margin-left: 10px;
        max-width: 170px;
      }
    }
    .social-col {
      padding: 0;
      text-align: center;
    }
  }
  .copyright{
    font-size: $font-size-small !important;
    font-weight: bold;
    bottom:0;
    color: #99bbc1;
    padding-top: 50px !important;
    white-space: nowrap;

    @media screen and (max-width: 1000px){
      margin: 0 auto !important;
      padding: 30px !important;
    }
  }
  .placeHolderFacebookIcon{
    width: 128px;
    height: 30px;
    border-radius: 6px;
    background-color: #3b5998;
  }
}
